.features-list {
  .list-reset();

  display: flex;
  flex-direction: column;
}

.features-list__item {
  position: relative;

  display: flex;
  align-items: flex-end;
  min-height: 125px;

  font-size: 17px;
  font-weight: 700;
  font-style: normal;
  line-height: 1.18;
  letter-spacing: normal;
  color: @black;

  &::before {
    content: "";
    position: absolute;
    top: 30px;

    background-repeat: no-repeat;
  }
}

.features-list__item--flower {
  &::before {
    left: 5px;

    width: 32px;
    height: 37px;

    background-image: url("../img/icon-feature-flower.svg");
  }
}

.features-list__item--money {
  &::before {
    left: 0;

    width: 47px;
    height: 27px;

    background-image: url("../img/icon-feature-money.svg");
  }
}

.features-list__item--heart {
  &::before {
    left: 5px;

    width: 47px;
    height: 27px;

    background-image: url("../img/icon-feature-heart.svg");
  }
}

.features-list__item--ball {
  &::before {
    left: 5px;

    width: 35px;
    height: 34px;

    background-image: url("../img/icon-feature-ball.svg");
  }
}

.features-list__item--rocket {
  &::before {
    left: -5px;

    width: 55px;
    height: 23px;

    background-image: url("../img/icon-feature-rocket.svg");
  }
}

.features-list__item--gift {
  &::before {
    left: 5px;

    width: 33px;
    height: 32px;

    background-image: url("../img/icon-feature-gift.svg");
  }
}

@media (min-width: @tablet-width) {
  .features-list {
    flex-wrap: wrap;
    justify-content: space-between;

    height: 460px;
  }

  .features-list__item {
    width: 295px;
    min-height: 150px;

    font-size: 20px;
  }

  .features-list__item::before {
    top: 50px;
  }
}

@media (min-width: @desktop-width) {
  .features-list {
    max-width: 700px;
    margin-left: 175px;
  }
}
